import React, { useEffect } from 'react';
import "./App.css";
import { Button } from '@mui/material';
import { initializeApp } from 'firebase/app';
import { getAuth, signInAnonymously } from 'firebase/auth';

const firebaseConfig = {
  apiKey: "AIzaSyA736xAgiNc4V1iYdvxOC8OyaYfCy0kuOI",
  authDomain: "mgsurvey-a06ea.firebaseapp.com",
  projectId: "mgsurvey-a06ea",
  storageBucket: "mgsurvey-a06ea.appspot.com",
  messagingSenderId: "783821287261",
  appId: "1:783821287261:web:d248ef3c9d7f4507f882b4",
  measurementId: "G-L55G7JJZJW"
};

const app = initializeApp(firebaseConfig); // Firebase 앱 초기화
const auth = getAuth(app); // 초기화된 Firebase 앱으로부터 인증 객체 가져오기

function setScreenSize() {
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty("--vh", `${vh}px`);
}

function App() {
  useEffect(() => {
    setScreenSize();
    const handleResize = () => setScreenSize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleLogin = () => {
    signInAnonymously(auth)
      .then(() => {
        // 로그인 성공 시 추가적인 작업 수행 가능
        console.log("익명 로그인 성공");
      })
      .catch((error) => {
        // 로그인 실패 시 에러 처리
        console.error("익명 로그인 실패:", error);
      });
  };

  return (
    <div className="App">
      <div>
        <p
          style={{
            marginTop: '10.84vh',
            fontSize: '10.3vw',
            fontWeight: 800,
            color: 'white'
          }}>
          쫀득쫀득<br />
          맛있는 망개랑 설문지
        </p>

        <p
          style={{
            marginTop: '10.84vh',
            fontSize: '4.9vw',
            fontWeight: 500,
            lineHeight: '36px',
            color: 'white'
          }}>
          설문조사를 완료해주신 분들 중 추첨을 통해 5분께 
          망굴세트M (26,000원 상당)을 드릴 예정이에요!
        </p>

        <Button
          variant="contained"
          color="primary"
          style={{
            marginTop: '20vh',
            fontSize: '16px',
            fontWeight: 'bold',
            color: 'white',
            backgroundColor: '#1976d2',
          }}
          onClick={handleLogin}
        >
          지금 시작하기
        </Button>
      </div>
    </div>
  );
}

export default App;
